    background-image: url(./static/arrow.svg);
    background-repeat: repeat-x;
    width: 45px; }
  40% {
    background-image: url(./static/arrow.svg);
    background-repeat: repeat-x;
    width: 35px; }
  100% {
    background-image: url(./static/arrow.svg);
    transform: scale(1.3);
    background-repeat: repeat-x;
    width: 24px; } }

@keyframes transformToArrow {
  20% {
    background-image: url(./static/arrow.svg);
    background-repeat: repeat-x;
    width: 45px; }
  40% {
    background-image: url(./static/arrow.svg);
    background-repeat: repeat-x;
    width: 35px; }
  100% {
    background-image: url(./static/arrow.svg);
    transform: scale(1.3);
    background-repeat: repeat-x;
    width: 24px; } }

